@media screen and (max-width: 767px) {
  .test {
    background: red; } }

.bubbles {
  display: flex;
  justify-content: center;
  position: absolute;
  bottom: 0;
  left: 50%;
  right: 0;
  margin-bottom: 30px; }
  @media screen and (max-width: 767px) {
    .bubbles {
      left: 0; } }

.bubble {
  margin: 5px;
  width: 20px;
  height: 20px;
  border-radius: 1000px;
  border: solid 3px #ffffff;
  transition: opacity .1s linear; }
  .bubble:hover {
    background-color: white;
    opacity: 0.3; }
  .bubble_active {
    background-color: white;
    opacity: 1; }
    .bubble_active:hover {
      background-color: white;
      opacity: 1; }
