.plansBlocksWrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 400px;
  background-color: #f8f9fc;
  color: #32325c; }
  @media screen and (max-width: 767px) {
    .plansBlocksWrapper {
      padding-top: 100px; } }
  .plansBlocksWrapper > p {
    font-size: 40px;
    margin: 0 20%;
    line-height: 1.6;
    text-align: center; }
  .plansBlocksWrapper__block {
    display: flex;
    flex-direction: row;
    justify-content: center; }
    @media screen and (max-width: 767px) {
      .plansBlocksWrapper__block {
        flex-direction: column;
        margin: 30px 8%; } }
    .plansBlocksWrapper__block__item {
      flex: 0 0 29%;
      margin: 15px;
      transition: all .5s ease; }
    .plansBlocksWrapper__block__item:hover {
      border-radius: 10px;
      box-shadow: 0 15px 40px rgba(0, 0, 0, 0.15);
      transform: scale(1.05); }

.block {
  display: flex;
  flex-direction: row; }
