  .test {
    background: red; } }

.navbar {
  background-color: #313a5a;
  display: flex;
  position: relative;
  justify-content: space-around;
  width: 100%;
  padding-top: 25px;
  font-family: 'bariol', sans-serif; }
  @media screen and (max-width: 767px) {
    .navbar {
      justify-content: space-between;
      padding: 25px 20px 0 20px; } }
  .navbar__logo {
    display: flex;
    margin-right: 50px;
    font-size: 26px; }
    @media screen and (max-width: 767px) {
      .navbar__logo {
        display: none; } }
  .navbar__list {
    display: flex;
    flex: 0 0 40%;
    justify-content: center; }
    @media screen and (max-width: 767px) {
      .navbar__list {
        display: none; } }
  .navbar__right {
    display: flex; }
    .navbar__right > * {
      margin-right: 15px; }
      @media screen and (max-width: 767px) {
        .navbar__right > * {
          margin-right: 0; } }
  .navbar__item {
    display: flex;
    justify-content: center;
    padding: 0 10px; }
    @media screen and (max-width: 767px) {
      .navbar__item {
        width: 100%;
        padding-left: 86px; } }
    .navbar__item__link {
      display: flex;
      align-items: center;
      justify-content: center;
      color: white;
      background-color: transparent;
      transition: color 0.2s linear; }
      @media screen and (max-width: 767px) {
        .navbar__item__link > * {
          height: 50px;
          line-height: 50px; } }
  .navbar__toggle {
    outline: none;
    background-color: transparent;
    border: none;
    padding: 0;
    display: none;
    width: 30px;
    height: 24px; }
    .navbar__toggle > span {
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      height: 3px;
      width: 100%;
      background-color: #15484c;
      display: block;
      position: absolute;
      transition: opacity 0.3s; }
    @media screen and (max-width: 767px) {
      .navbar__toggle {
        display: inherit;
        position: absolute;
        right: 20px;
        top: 50%;
        transform: translateY(-50%);
        z-index: 6; }
        .navbar__toggle:before {
          content: '';
          top: 0;
          left: 0;
          height: 3px;
          width: 100%;
          background-color: #15484c;
          display: block;
          position: absolute;
          transition: all 0.3s; }
        .navbar__toggle:after {
          content: '';
          bottom: 0;
          left: 0;
          height: 3px;
          width: 100%;
          background-color: #15484c;
          display: block;
          position: absolute;
          transition: all 0.3s; } }
  .navbar_showed .navbar__menu {
    transform: scaleY(1); }
  .navbar_showed .navbar__toggle > span {
    opacity: 0;
    visibility: hidden; }
  .navbar_showed .navbar__toggle:before {
    top: 50%;
    transform: translateY(-50%) rotate(-45deg); }
  .navbar_showed .navbar__toggle:after {
    bottom: 50%;
    transform: translateY(50%) rotate(45deg); }
