.main {
  max-width: 2100px;
  position: absolute;
  top: 680px;
  margin: 0 5%;
  padding: 50px 65px;
  left: 0;
  right: 0;
  background-color: white;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  border-radius: 10px;
  box-shadow: 0 10px 34px 0 rgba(210, 210, 210, 0.5); }
  @media screen and (max-width: 1023px) {
    .main {
      padding: 45px 35px; } }
  @media screen and (max-width: 767px) {
    .main {
      position: static; } }
  @media (max-width: 881px) and (min-width: 768px) {
    .main {
      top: 750px; } }
  .main__wrapper {
    position: relative;
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: space-between; }
    @media screen and (max-width: 767px) {
      .main__wrapper {
        flex-direction: column; } }
