@media screen and (max-width: 767px) {
  .test {
    background: red; } }

.customerSecion {
  position: relative;
  height: 970px;
  margin: 50px 0;
  width: 100%;
  overflow: hidden; }
  @media screen and (max-width: 767px) {
    .customerSecion {
      height: 800px; } }

.container {
  width: 100%;
  height: 970px;
  display: flex;
  position: absolute;
  top: 0;
  left: 0; }
  @media screen and (max-width: 767px) {
    .container {
      height: 800px; } }
  .container__left {
    margin: 0 auto;
    max-width: 380px;
    display: flex;
    justify-content: space-between;
    flex: 0 0 43.75%; }
    @media screen and (max-width: 1023px) {
      .container__left {
        justify-content: center;
        flex-basis: 35%; } }
    @media screen and (max-width: 767px) {
      .container__left {
        display: none; } }
  .container__right {
    position: relative;
    flex: 0 0 56.25%;
    height: 100%;
    background-color: #313a5a; }
    @media screen and (max-width: 1023px) {
      .container__right {
        flex-basis: 65%; } }
    @media screen and (max-width: 767px) {
      .container__right {
        flex-basis: 100%; } }

.slider {
  position: absolute;
  top: 50%;
  left: 35%;
  transform: translateY(-50%); }
  @media screen and (max-width: 767px) {
    .slider {
      left: 0; } }
  .slider_wrapper {
    display: flex;
    transition: transform 400ms cubic-bezier(0.455, 0.03, 0.515, 0.955); }

.bubbles {
  display: flex;
  justify-content: center;
  position: absolute;
  bottom: 0;
  left: 50%;
  right: 0;
  margin-bottom: 30px; }
  @media screen and (max-width: 767px) {
    .bubbles {
      left: 0; } }
