@media screen and (max-width: 767px) {
  .test {
    background: red; } }

.card {
  perspective: 1000px;
  height: 640px;
  width: 100%;
  position: relative; }
  .card__side {
    transition: all .8s ease;
    backface-visibility: hidden;
    height: 640px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between; }
    .card__side_front {
      transform: rotateY(0deg); }
    .card__side_back {
      transform: rotateY(180deg);
      top: 0;
      position: absolute; }
    .card__side__item {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: flex-start;
      margin-top: 40px; }

.title > ul {
  margin-bottom: 60px; }

.title > ul > li:first-child {
  margin-bottom: 40px; }
  @media screen and (max-width: 767px) {
    .title > ul > li:first-child {
      margin-bottom: 30px; } }

.title__head {
  font-size: 24px;
  line-height: 1.33;
  letter-spacing: 0.3px;
  text-align: center;
  color: #32325c; }

.title__price {
  font-size: 72px;
  line-height: 0.89;
  text-align: center;
  color: #1fdb84; }

.title__period {
  margin: 20px 60px;
  font-size: 16px;
  line-height: 2;
  letter-spacing: 0.2px;
  text-align: center;
  color: #5b6c94; }

.option__list > li {
  margin: 20px 60px;
  font-size: 16px;
  line-height: 2;
  letter-spacing: 0.2px;
  text-align: center;
  color: #5b6c94; }
  @media screen and (max-width: 1023px) {
    .option__list > li {
      margin: 20px 40px; } }

.button {
  margin-bottom: 40px; }
  @media screen and (max-width: 1023px) {
    .button {
      width: 160px;
      height: 40px;
      font-size: 16px;
      border-radius: 5px 0px 5px; } }

.Front {
  transform: rotateY(-180deg); }

.Back {
  transform: rotateY(0deg); }
