.next, .prev {
  position: absolute;
  top: 80%;
  right: 10%;
  opacity: 0.5;
  font-size: 20px;
  color: #fff;
  border: 2px solid #fff;
  background-color: transparent;
  cursor: pointer;
  padding: 13px 15px;
  border-radius: 50%;
  outline: none; }

.next:hover, .prev:hover {
  opacity: 1; }

.prev {
  left: 10%; }
