@media screen and (max-width: 767px) {
  .test {
    background: red; } }

.big {
  width: 160px;
  height: 410px;
  clip-path: polygon(0 0, 100% 0, 100% 50%, 0 100%); }
  @media screen and (max-width: 1023px) {
    .big {
      width: 110px;
      height: 265px; }
      .big:first-child {
        margin-right: 20px; } }

.small {
  width: 18px;
  height: 40px;
  clip-path: polygon(0 0, 100% 0, 100% 50%, 0 100%); }

.lightGreen {
  background-color: rgba(31, 219, 132, 0.1); }

.green {
  background-color: #1fdb84; }

.white {
  background-color: white; }
