.menu {
  display: flex;
  flex-flow: row;
  padding: 0;
  margin: 0;
  justify-content: space-around;
  max-width: 1024px;
  height: 100%;
  width: 70%; }
  @media screen and (max-width: 767px) {
    .menu {
      flex-flow: column;
      color: black; } }

.item {
  display: flex;
  justify-content: center; }
  @media screen and (max-width: 767px) {
    .item {
      width: 100%;
      padding-left: 86px; } }
  .item__link {
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    background-color: transparent;
    transition: color 0.2s linear; }
    @media screen and (max-width: 767px) {
